export default{
    //首页
    content:(state)=>{
        state.hName='微风向首页'
    },
    //贴纸
    tDiy:(state)=>{
        state.hName='贴DIY'
    },
    //美图
    mDiy:(state)=>{
        state.hName='美DIY'
    },
    //个人中心
    mycontent:(state,payload)=>{
        if(payload == '/ucenter'){
            state.hName='会员中心'
        }else if(payload == '/ucenter/login'){
            state.hName='登录'
        }else if(payload == '/ucenter/register'){
            state.hName='注册'
        }
       
    },

    //登录
    login:(state)=>{
        state.hName='登录'
    },
    //用户信息
    uInfo:(state)=>{
        state.hName='个人资料'
    },
    //注册
    reg:(state)=>{
        state.hName='注册'
    },
    //美拍
    mPhoto:(state,payload)=>{
        if(payload == 'meiP'){
            state.hName='美拍'
        }else if(payload == 'meiY'){
            state.hName='美言'
        }
       
    },
    //获取数据
    datalist:(state,payload)=>{
        state.dataIndex = payload;
        // console.log(payload)
    },
    //详情页
    detail:(state,payload)=>{
        if(payload == "tieP"){
            state.hName='贴DIY详情'
        }else if(payload == "meiP"){
            state.hName='美拍详情'
        }else if(payload == "meiY"){
            state.hName='美言详情'
        }
       
    },
    //退出
    layout:(state)=>{
        state.isLogin = false;
    },
    //用户信息
    user:(state,payload)=>{
        state.user = payload
    },
    //清除用户信息
    usernull:(state)=>{
        state.user ={};
    }
}


