import Vue from 'vue'
export default{
    //首页
    content:({commit,state})=>{
        commit('content')
    },
    //贴纸
    tDiy:({commit,state})=>{
        commit('tDiy')
    },
    //美图
     mDiy:({commit,state})=>{
        commit('mDiy')
    } ,
    //用户中心
    mycontent:({commit,state},payload)=>{
        commit('mycontent',payload)
    }, 
    // 登录
    login:({commit,state})=>{
        commit('login')
    } ,
    // 个人信息
    uInfo:({commit,state})=>{
        commit('uInfo')
    },
    //注册
    reg:({commit,state})=>{
        commit('reg')
    },
    //美拍
    mPhoto:({commit,state},payload)=>{
        commit('mPhoto',payload)
    },
    //读取数据
    datalist:({commit,state},payload)=>{
        // console.log(payload)
        commit('datalist',payload)
    },
    //详情
    detail:({commit,state},payload)=>{
        commit('detail',payload)
    },
    //退出
    layout:({commit,state})=>{
        commit('layout')
    },
    //用户信息
    user:({commit,state},payload)=>{
        commit('user',payload)
    },


    //清除用户信息
    usernull:({commit,state})=>{
        commit('usernull')
    }
    //获取所有信息





}


