const content=()=>import(/* webpackChunkName: "content" */ '../components/content.vue')
const tiediy=()=>import(/* webpackChunkName: "tiediy" */ '../components/tiediy.vue')
const meidiy=()=>import(/* webpackChunkName: "meidiy" */ '../components/meidiy.vue')
const login=()=>import(/* webpackChunkName: "login" */ '../components/user_login.vue')
const register=()=>import(/* webpackChunkName: "register" */ '../components/user_reg.vue')
const mtlist=()=>import(/* webpackChunkName: "mtlist" */ '../components/mtlist.vue')
const uinfo=()=>import(/* webpackChunkName: "uinfo" */ '../components/user_info.vue')
const ucenter=()=>import(/* webpackChunkName: "ucenter" */ '../components/user_center.vue')
const detail=()=>import(/* webpackChunkName: "ucenter" */ '../components/imgDtail.vue')

let routes=[
      {path:'/content',component:content},
      {path:'/',redirect:'/content'},
      {path:'/tiediy',component:tiediy},
      {path:'/meidiy',component:meidiy},
      {path:'*/login',component:login},
      {path:'*/register',component:register},
      {path:'*/mtlist',component:mtlist},
      {path:'*/uinfo',component:uinfo},
      {path:'*/ucenter',component:ucenter},
      {path:'*/detail',component:detail},
      
      
      
]
export default{routes}


