export default{
    hName:(state)=>{
        return state.hName;
    },
    isLogin:(state)=>{
        return state.isLogin;
    },
    dataIndex:(state)=>{
        return state.dataIndex
    },
    user:(state)=>{
        return state.user
    }
}


